const TYPES = {
  string: 'String',
  number: 'Number',
  expression: 'Expression'
}

const EXPRESSIONS = {
  cin: 'cin',
  cout: 'cout',
}

class AST {
  constructor () {
    this.global = Object.create(null)
    this.stack = new Array()
  }
  
  addVol (vol) {
    this.global[vol.name] = vol
  }

  addAction (action) {
    this.stack.push(action)
  }
}

class Terminal {
  getInput () {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        let value
        while(!value) {
          value = prompt('Write value')
        }
        resolve(value)
      }, 500)
    })
  }
}

class Exceptions {
  static typeError ({ value, type }) {
    throw new TypeError(`Cannot set value ${value} of type ${type}`)
  }
  static primitiveAssignError ({ type }) {
    throw new Error(`Cannot set value of primitive type ${type}`)
  }
  static referenceError ({ name }) {
    throw new ReferenceError(`${name} is not defined`)
  }
}

class Primitive {
  constructor ({ name, type, value }) {
    this._name = name
    this._type = type
    this._value = value

    if (!type) {
      this.defineType()
    }
  }

  set value (val) {
    if (this._type === TYPES.string) {
      this._value = String(val)
    } else if (this._type === TYPES.number && Number(val)) {
      this._value = Number(val)
    } else {
      Exceptions.typeError({ value: val, type: this._type })
    }
  }

  set name (name) {
    this._name = name
  }

  set type (type) {
    this._type = type
  }

  get value () {
    return this._value
  }

  get name () {
    return this._name
  }

  get type () {
    return this._type
  }

  defineType () {
    if (String(this.value).charAt(0) === '"') {
      this.type = TYPES.string
    } else if (!isNaN(this.value)) {
      this.type = TYPES.number
    }
  }
}

const ast = new AST()
const terminal = new Terminal()


function excludeSpaces (program) {
  return program.trim()
}

function parseExpression (program) {
  let match, expr
  program = excludeSpaces(program)

  if (match = /^vol\s*(\w+)\s*=\s*(\d+)\b/.exec(program)) {
    expr = new Primitive({ type: TYPES.number, name: match[1], value: Number(match[2])})
    ast.addVol(expr)
  } else if (match = /^vol\s*(\w+)\s*=\s*"(\w+?)"/.exec(program)) {
    expr = new Primitive({ type: TYPES.string, name: match[1], value: match[2]})
    ast.addVol(expr)
  } else {
    match = parseAction(program)
  }

  let remainProgram = program.slice(match[0].length)
  if (remainProgram.length) {
    parseExpression(remainProgram)
  }

  return ast
}

function parseAction (program) {
  let match, expr
  program = excludeSpaces(program)
  
  if (match = /^c\.(out|in)\((.+)\)/.exec(program)) {
    expr = { 
      type: TYPES.expression,
      args: parseArguments(match[2]),
      kind: 'c' + match[1]
    }
    ast.addAction(expr)
  } else {
    throw SyntaxError(`Unexpected syntax ${program}`)
  }

  return match
}

function parseArguments (program) {
  let args = program.split(',').map(defineArgument)
  return args
}

function defineArgument (val) {
  let arg = {}
  let value = val.trim()

  if (String(value).charAt(0) === '"') {
    arg = new Primitive({ value: String(value).replace(/\"/g, '') })
  } else if (!isNaN(value)) {
    arg = new Primitive({ value: Number(value) })
  } else {
    arg = new Primitive({ name: value }) // Remove actions with ast
  }

  return arg
}

async function interpret (ast) {
  if (!ast.stack.length) return
  console.log('stack', ast.stack)

  for (const action of ast.stack) {
    if (action.type === TYPES.expression) {
      const args = evaluate(action.args)
      await topScope[action.kind](action, args)
    }
  }
}

function evaluate (args) {
  return args.map(arg => ast.global[arg.name])
}

const topScope = Object.create(null)

topScope.cin = async function (action, args) {
  if (!args) throw SyntaxError(`Invalid number of arguments - ${args.length}`)

  let i = 0;

  while(i < args.length) {
    let inputVal = await terminal.getInput()
    let arg = args[i]

    console.log('<< ', arg.name)

    if (arg.name) {
      ast.global[arg.name].value = inputVal  // Remove actions with ast
    } else {
      Exceptions.primitiveAssignError({ type: arg.type })
    }

    i++
  }

  console.log(ast)
  return action
}

topScope.cout = async function (action, args) {
  if (!args) throw SyntaxError(`Invalid number of arguments - ${args.length}`)

  let i = 0;

  while(i < args.length) {
    let arg = args[i]

    if(arg?.name) {
      console.log('>> ', arg.value)
    } else {
      Exceptions.referenceError({ name: arg.name })
    }

    i++
  }

  console.log(ast)
  return action
}


